import React, { useRef, useState,Suspense,useMemo } from 'react'
import { Canvas, useFrame } from '@react-three/fiber'
import { useLoader } from '@react-three/fiber'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'
import DracoScene from './components/Dracoscene'
import { useGLTF } from '@react-three/drei/core/useGLTF'
import dracoscene from './model/dracoscene.gltf'
import {OrbitControls} from '@react-three/drei'
import Styled from 'styled-components'
import GlobalStyle from './components/globalStyle'
import { useThree } from '@react-three/fiber'
import * as THREE from 'three';
import img from './model/planetxtures/Material1.jpg'
import ambientOcclution from './model/planetxtures/ambient-occlusion.jpg'
import normalMapImg from './model/planetxtures/NormalMap.png'
import baseColour from './model/planetxtures/base-color.jpg'
import roughness from './model/planetxtures/roughness-map.jpg'
import heightMap from './model/planetxtures/height-map.png'
import { CubeTextureLoader } from "three";
import nx from './components/environment/nx.jpg'
import ny from './components/environment/ny.jpg'
import nz from './components/environment/nz.jpg'
import px from './components/environment/px.jpg'
import py from './components/environment/py.jpg'
import pz from './components/environment/pz.jpg'


function App() {


 

  //cube
  const CubeTexture =()=>{
    const { scene } = useThree();
    const loader = new CubeTextureLoader();
    const texture = loader.load([
      px,
      nx,
      py,
      ny,
      pz,
      nz,
    ]);
  
    texture.encoding = THREE.sRGBEncoding
    scene.background = texture;
    return null;

  }

  //PLane model
  const PlaneModel =()=>{
    
    const plane = useRef();
    const [colorMap,ambientOcclusionMap,normalMap,roughnessMap,height] = useLoader(THREE.TextureLoader,[baseColour,ambientOcclution,normalMapImg,roughness,heightMap]);
    
    
    return (
    <mesh   rotation-x={Math.PI * -0.5}  position={[10,-2.5,-70]} scale={[7,10,12]} >
      <planeBufferGeometry attach="geometry"  args={[64, 64, 74, 74]} />
      <meshPhysicalMaterial 
        attach="material" 
        map={colorMap}
        displacementMap={height}
        normalMap={normalMap}
        metalness={0.5}
        roughness={1.0}
        
         />
   </mesh>
    )

  }

  

  //windmill model
  const [speed,setSpeed] = useState(0);
  

  const WindModel = ({ ...props })=>{
    useFrame((state) => {
      state.camera.position.z = 50 + Math.sin(state.clock.getElapsedTime()) * 30
      state.camera.updateProjectionMatrix()
    })
    const group = useRef()
    const pole = useRef()
    const fan = useRef()


    
    useFrame( (state) => {
      const t = state.clock.getElapsedTime()
      fan.current.rotation.z=speed*t;
     
      group.current.rotation.y=t;
       })
    const { nodes, materials } = useGLTF(dracoscene)
    const { viewport } = useThree()
    useMemo(() => {
      
      // materials.Windmill.transparent = true
      
      // materials.Windmill.opacity = 0.5
    }, [materials])
      
    return (
        <group   ref={group} {...props} dispose={null}  scale={(viewport.width / 5)} >
        
      <group rotation={[-Math.PI / 2, 0, 0]}>
        <group rotation={[Math.PI / 2, 0, 0]}>
          <group scale={[0.4, 0.4, 0.47]} >
            <mesh   ref={pole} geometry={nodes.Object_4.geometry} material={materials.Windmill} />
          </group>
          <group position={[0, 13.16, 0.56]} scale={[0.4, 0.4, 0.47]} >
            <mesh ref={fan} geometry={nodes.Object_6.geometry} material={materials.Windmill} />
          </group>
        </group>
      </group>
    </group>
    )
     
  }

  

  return (
    
  <div>
  <GlobalStyle/>
  <StyledHeader>
  <h1>WindMill</h1>
  </StyledHeader>
  <StyledCanvas style={{ width: "80%", height: "60vh" }}>
  <Canvas>
  <ambientLight intensity={2} />
  <spotLight   
   

    intensity={4} 
    angle={0.1} penumbra={1} 
    position={[10, 15, 10]} 
   />
  <Suspense fallback={null}>
  <CubeTexture/>
  <WindModel  position={[16, -4, -80]}/>
  <WindModel position={[56, -4, -120]}/>
  <WindModel position={[-26, -4, -40]}/>
  <WindModel position={[-46, -4, -140]}/>
  <WindModel position={[-86, -4, -180]}/>
  <WindModel position={[-26, -4, -200]}/>
  <WindModel position={[-46, -4, -240]}/>
  <WindModel position={[-86, -4, -280]}/>
  <WindModel position={[-86, -4, -300]}/>
  <WindModel position={[-26, -4, -340]}/>
  <WindModel position={[-46, -4, -380]}/>
  <WindModel position={[-86, -4, -400]}/>
  <OrbitControls/>
  <PlaneModel/>
  </Suspense>
  </Canvas>

  </StyledCanvas>
  <StyledDescription>
    <button onClick={() =>{setSpeed(speed+5)}}>increase speed</button>
    <button onClick={() =>{setSpeed(speed-5)}}>Decrease speed</button>

  </StyledDescription>

    </div>
  );
}

const StyledHeader = Styled.div`
font-family: 'Luxurious Roman', cursive;
  text-align: center;
  margin-top:-1em ;
  font-size: 1em;
  
`
const StyledCanvas = Styled.div`

margin: 0 auto;
margin-top:-0.5em;
border: 2px solid #975416;
`
const StyledDescription = Styled.div`
  display:flex;
  margin-top:0.3em;
  button{
   
    margin: 0 auto;
    font-size:1.5em;
    padding:1em;
  }
`

export default App;
